export default {
	data() {
		return {
			windowHeight: '',
			checkoff: false,
			id: "",
			infor: [],
			nowtime: '',
			arr: [],
			xz: "../../static/user/xz.png",
			wxz: "../../static/user/wxz.png",
		};
	},
	onLoad() {
		var that = this;
		that.windowHeight = this.statusBarHeight + 44;

		uni.getStorage({
			key: 'userinfo',
			success: function(res) {
				that.id = res.data.id;
				that.getinfo();
			}
		})
	},
	methods: {
		back: function() {
			uni.navigateBack({
				delta: 1
			});
		},
		getinfo: function() {
			let that = this;
			uni.request({
				url: that.serveipd + "/api/merchant/auth/selectMessageList",
				method: 'GET',
				data: {
					merchantId: that.id
				},
				success: res => {
					that.infor = res.data.data;
					console.log(res);
				},
				fail: () => {},
				complete: () => {}
			});
			let time = new Date();
			let y = time.getFullYear();
			let m = time.getMonth() + 1;
			let d = time.getDate();
			if (m < 10) {
				m = "0" + m
			};
			if (d < 10) {
				d = "0" + d
			}
			that.nowtime = y + "-" + m + "-" + d;
			console.log(that.nowtime)
		},
		check_box: function(id) {
			console.log(id);
			let a = this.arr.indexOf(id);
			if (a != -1) {
				this.arr.splice(this.arr.indexOf(id), 1)
			} else {
				this.arr.push(id);
			}
		},
		allElection: function() {
			if (this.arr.length == this.infor.length) {
				this.arr = [];
			} else {
				this.arr = [];
				for (let i = 0; i < this.infor.length; i++) {
					this.arr.push(this.infor[i].id)
				}
			}
		},
		del: function(e) {
			let that = this;
			uni.request({
				url: that.serveipd + "api/merchant/auth/deleteMessageList",
				// header: {
				// 	'Content-type': 'application/x-www-form-urlencoded'
				// },
				method: "POST",
				data: {
					ids: that.arr
				},
				success(res) {
					if(res.code==0){
						that.getinfo();
					}
				}
			})

		}
	},
}
